function _refresh(forceRefresh, con, data) {

    var self = this
    var container = con || self.__params.container

    //
    if (forceRefresh) {

        var controls = (data || self.__params.controls).map(x => {
            x.isNew = false
            //FIXME:
            // return comLib.isObj(x) ? x : defControl.filter(z => z.id == x)[0]
            return comLib.isObj(x) ? x : self.defaultControl.filter(z => z.id == x)[0]
        })
        var html = controls
            .map(x => _generateToolDom(x, self.options.templates))
        container.append(html);

    }
    // TODO:   
    else {
        var needAddItems = data || self.__params.needAppendDom;
        for (let i = 0; i < needAddItems.length; i++) {
            const item = needAddItems[i];
            item.isNew = false
            var beforeDom;
            var parentContainer
            if (item.parentID) {
                var menuContainer = container.find(`.control[data-control-id=${item.index}]`).siblings('.dropdown-menu');
                beforeDom = menuContainer.find(`.control[data-control-id=${item.beforeID}]`).parent('.controlBarItemContainer')
                parentContainer = menuContainer
            } else {
                beforeDom = container.find(`.control[data-control-id=${item.beforeID}]`).parent('.controlBarItemContainer')
                parentContainer = container
            }
            if (!beforeDom || beforeDom.length == 0) {
                parentContainer.append(item.html)
            } else {
                $(item.html).insertBefore(beforeDom)
            }

        }
    }

}

