//idcontrolobj
function getControlDef(id, controlDefList) {
    if (id == undefined) {
        return null;
    }
    var res;
    for (let i = 0; i < controlDefList.length; i++) {
        const def = controlDefList[i];
        if (def.id == id) {
            res = def
            break;
        }
        if (def.nodes) {
            res = getControlDef(id, def.nodes)
            if (res) {
                break;
            }
        }
    }
    return res
}

