function _bindEvents() {
    var self = this;
    var container = this.__params.container
    //control click
    container.delegate(".control", "click", function () {


        var $this = $(this);
        var id = $this.attr('data-control-id');

        self.injects.controlBarContainer.find('.groupContainer .dropdownArrow').removeClass(self.options.closeArrow).addClass(self.options.openArrow);
        self.injects.controlBarContainer.find('.groupContainer .dropdown-menu').removeClass('active');
        //group
        if ($this.attr('isGroup')) {
            //
            if ($this.hasClass('isOpen')) {
                $this.removeClass('isOpen')

            } else {//
                $this.addClass('isOpen')
                $this.find('.dropdownArrow').removeClass(self.options.openArrow).addClass(self.options.closeArrow)
                $this.siblings('.dropdown-menu').addClass("active");
            }

        } else { //control

            var control = self.getControl(id);
            self.activeControl(control || id);
        }

    });
    container.delegate(".control", "mouseover", function () {

        var $this = $(this);
        var _icon = $this.attr('data-icon');
        var _hovr = $this.attr('data-iconHover') || _icon;
        $this.find('.controlIcon').removeClass(_icon).addClass(_hovr);

    });
    container.delegate(".control", "mouseout", function () {

        var $this = $(this);
        var _icon = $this.attr('data-icon');
        var _hovr = $this.attr('data-iconHover') || _icon;
        $this.find('.controlIcon').removeClass(_hovr).addClass(_icon);
    });
}

