
//  YYYY-MM-DD
const formatDate = function(time) {
	const d = time ? new Date(time) : new Date();
	try {
		let h = d.getHours() < 10 ? "0" + d.getHours() : d.getHours();
		let m = d.getMinutes() < 10 ? "0" + d.getMinutes() : d.getMinutes();
		let s = d.getSeconds() < 10 ? "0" + d.getSeconds() : d.getSeconds();
		return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + h + ':' + m + ':' + s;
	} catch(error) {

		return time;
	}
};

//  
const delateArrItem = function(arr, item) {
	let Arr = arr;
	for(let i = 0; i < Arr.length; i++) {
		if(this[i] == item) {
			this.splice(i, 1);
			break;
		}
	}
	return Arr
};

/*******
 * num
 * precision
 * separator
 * eg:formatNumber(100000000) "100,000,000" 
 * formatNumber(100000000,2); "100,000,000.00"
 * formatNumber(100000000,2,"?"); "100?000?000.00"
 *********/
function formatNumber(num, precision, separator) {
    let parts;
    // 
    if (!isNaN(parseFloat(num)) && isFinite(num)) {
        //  .5, 5. 0.5, 5, , 
        //  if (!isNaN(num = parseFloat(num)) && isFinite(num))
        // parseFloat,  parseFloat(12312312.1234567119)
        //  12312312.123456713
        let n  =new Number(num);
        // 
        n = (typeof precision !== 'undefined' ? n.toFixed(precision) : n).toString();
        // 
        parts = n.split('.');
        // [separator], 
        parts[0] = parts[0].toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1' + (separator || ','));
 
        return parts.join('.');
    }else{
    	console.error(`NaN: ${num}`);
    	return NaN;
    }
    
}
function dataToNumber(num,sorted) {  //
	
	
	let sort = false;  //
	for(let ele of arguments){
		
		if(ele == true || ele=="sort" || ele=="sorted" || ele=="true" || ele=="True" || ele=="T" || ele=="t"){
			sort = true
		}
	}
	 let sequence = function (a, b) {
	 	if(a > b) {
	 		return 1;
	 	} else if(a < b) {
	 		return -1
	 	} else {
	 		return 0;
	 	}
	 }
	
    if(typeof(num) != "object"){   //

    	if (!isNaN(parseFloat(num)) && isFinite(num)){
    		let data  =new Number(num);
    		return data
    	}else{
    		console.error(`NaN: ${num}`);
    		return `NaN: ${num}`;
    	}
    	
    }else{   //
    	let dataArr = num;
    	let request=[];
    	for(let obj of dataArr){
    		if (!isNaN(parseFloat(obj)) && isFinite(obj)){
    			
	    		request.push(new Number(obj));
	    	}else{
				console.error(`NaN: ${obj}`);
	    		request.push(`NaN: ${obj}`);
	    	}
    	}
    	sort ? request = request.sort(sequence) : null;
    	
    	return request;
    }
}

//  vuex Action
//      vuex strorevuex 
/*	1,$this  ---   axios vuex  config
	2,data   ---     string Array number 
	3,url    --- 
	return   ----promise data = >{}  */
const getDics = function($this,data,url) {
			
			if(!$this){
				console.error("");
				return ;
			}
			
			let inputArr=[];
			if(typeof(data) == "string" || typeof(data) == "number"){   //
				inputArr=[data];
			}else if(typeof(data) == "object" && data.length){
				inputArr=data;
			}else{
				console.error("");
				return ;
			}
			let hasdDicts = $this.$store.state.DictArr;
			let toServer = inputArr;
			let req= [];                   // vuex strorevuex 
			if(hasdDicts.length > 0){
				for(let i = inputArr.length - 1; i >= 0; i--){
					let objTest = inputArr[i].toUpperCase();
					
					hasd:
					for(let ele of hasdDicts){
						let typeTest = ele.type.toUpperCase();
						if(objTest == typeTest ){   //
							toServer.splice(i,1);
							req.push(ele);
							break hasd;
						}
					}

				}
			}else{
					toServer = inputArr;
					req= []; 
			}
			

			let reqServer = [];
			const urlStr = "api/Dictionary/GetDictionaries";
			
			let ret ;
			
			if(toServer.length>0){
				ret = $this.$axios.post($this.$config.serverUrl+urlStr, 
					    JSON.stringify(toServer),
					    {headers: {'Content-Type': 'application/json'}}
					 ).then((res) => {
								reqServer = res.data;
								let dataArr = req.concat(reqServer);
								$this.$store.commit('setDicts',reqServer);
								return dataArr;
					    }).catch(error=>{
					        console.error(error);
					});
			}else{
				reqServer = [];
				let dataArr = req.concat(reqServer);
				ret = Promise.resolve(dataArr);

			}
			console.log($this.$store.state.DictArr);
			return ret;
	
};
export default {
	formatDate,
	delateArrItem,
	getDics,
	formatNumber,
	dataToNumber,
}



