<template>
	<div>
							<div style="margin-bottom:20px">
								 <el-row>
				                 	<el-col :span="12">
				                        <el-input size= "small" v-model="keywordXB" placeholder="" class="handle-input mr10"></el-input>
				                    </el-col>
				                 	<el-col :span="12">
				                        <el-button size= "small" type="primary" icon="el-icon-search" @click="getDataXB"></el-button>
						                <el-button size="small" 
						                	type="primary" 
						                	v-show="state == 1 || state == 2"
						                	:disabled ="state != 1 && state != 2"
						                	icon="el-icon-plus" 
						                	@click="toSubcompartment(null,'edit')"></el-button>
						                <el-button size="small" 
						                	v-show="state == 1 || state == 2"
						                	:disabled ="state != 1 && state != 2"
						                	type="primary" 
						                	icon="el-icon-delete" 
						                	class="handle-del mr10" @click="delAllXB"></el-button>
				                    </el-col>
				                    
				                 </el-row>
					    		
				                
					    	</div>
					    	
					    	 <el-table 
				            	v-loading="loading"
				            	:data="xiaoBanData" 
				            	highlight-current-row
				            	border 
				            	class="table" 
				            	ref="multipleTableXB" 
				            	@selection-change="handleSelectionChangeXB">
				                <el-table-column 
				                	type="selection" 
				                	width="55" 
				                	align="center"></el-table-column>
				                </el-table-column>
				                <el-table-column prop="xbh" label="" align="center" width="130">
				                </el-table-column>
				                <el-table-column prop="tfh" label=""  align="center" >
				                </el-table-column>
				                <el-table-column prop="xbmj" label="(:)"  align="center" >
				                </el-table-column>
				                <el-table-column label="" align="center" width="200">
				                    <template slot-scope="scope">
				                    	<!--<el-button  
				                        	type="text" 
				                        	icon="fa fa-eye" 
				                        	@click="toSubcompartment(scope.row,'watch')">&nbsp;</el-button>
				                        <el-button  
				                        	type="text" 
				                        	v-show="state == 1 || state == 2"
				                			:disabled ="state != 1 && state != 2"
				                        	icon="el-icon-edit" 
				                        	@click="toSubcompartment(scope.row,'edit')"></el-button>-->
				                        <el-button 
				                        	type="text" 
				                        	size="small"
				                        	v-show="state == 1 || state == 2"
				                			:disabled ="state != 1 && state != 2"
				                        	icon="el-icon-delete" 
				                        	class="red" 
				                        	@click="xiaoBanDelete(scope.row)"></el-button>
				                    </template>
				                </el-table-column>
				            </el-table>
					    	<div class="pagination">
								 <el-pagination
							      @size-change="handleSizeChangeXB"
							      @current-change="handleCurrentChangeXB"
							      :current-page="currentPageXB"
							      :page-sizes="[5, 10, 15, 20]"
							      :page-size="pageSizeXB"
							      layout="total, sizes, prev, pager, next, jumper"
							      :total="totalPageXB">
							    </el-pagination>
				            </div>
	</div>
</template>

<script>
	export default{
		props:["XMBH","state"],
		data(){
				return {
					loading:true,
					multipleSelectionXB:[],
					xiaoBanData:[{  //
				                	xbh:"",
				                	tfh:"",
				                	xbmj:"",
				                }],
				    currentPageXB: 1,
	                pageSizeXB:5,
	                totalPageXB:0,
	                keywordXB:null,
				}
			},
			/*mounted(){
				this.getDataXB();
			},*/
			watch:{
				"XMBH":function(){
					this.XMBH ? this.getDataXB():null;
					console.log(this.XMBH);
				}
			},
			methods:{
			getDataXB(){
            	const value = {
	                	    xmbh:this.XMBH,
	                	    keyword:this.keywordXB,
		                	pageQueryInfo:{
				                	pageSize: this.pageSizeXB,
				    				pageIndex: this.currentPageXB
		                	},
	                };
	                console.log(JSON.stringify(value));
            	this.$axios.post(this.$config.serverUrl+"api/YZL_ZLXB_PY/GetYZL_ZLXB_PYsByXmbh", 
                JSON.stringify(value),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                	console.log(res);
	                    this.xiaoBanData = res.data.resultValue;
	                    this.totalPageXB=res.data.count;
	                    this.loading= false;
	                }).catch(error=>{
	                	this.$message.error(error);
                	})
            },	
			handleSelectionChangeXB(val){
            	this.multipleSelectionXB =val;
           	},
           	handleSizeChangeXB(val){
        		this.pageSizeXB = val ;
        		this.getDataXB();
        	},
            // 
            handleCurrentChangeXB(val){
                this.currentPageXB = val;
                this.getDataXB();
            },
           	toSubcompartment(data,type){
	            	console.log(data);
	
	            	this.$store.commit("setXmbh",data? data.xmbh : this.XMBH);
            		this.$router.push({
            						name:"SubcompartmentWorkSpace",
            						params:{
            							type,
            							XBkeyword:data ? (data.xbh || null) : null,
            							xmbh: data ? data.xmbh : this.XMBH,
            							ToState:3,
            							ToName:""}
            						});
            		//
            		this.$emit('update:drawer',false)
           		},
           	    delAllXB(){
	            	if(this.multipleSelectionXB.length<1){
	            		this.$message.error("");
	            	}else{

            		this.$confirm(', ?', '', {
			          confirmButtonText: '',
			          cancelButtonText: '',
			          type: 'warning'
			        }).then(() => {
						let delArrAllXB=[];
	            		for(let i of this.multipleSelectionXB){
	            			delArrAllXB.push(i.id)
	            		}
	            		this.multipleSelectionXB = [];

		                this.$axios.post(this.$config.serverUrl+"api/YZL_ZLXB_PY/DeleteYZL_ZLXB_PYs/", 
            							JSON.stringify(delArrAllXB),
					                	{headers: {'Content-Type': 'application/json'}}
					               ).then((res) => {
					                	this.getDataXB();
					                	this.$message.success("");
					                	
					                }).catch(error=>{
					                
		                				this.$message.error(error);
		                			})
						
						
			        }).catch(() => {
			        	
			        	this.$refs.multipleTableXB.clearSelection();
			          this.$message({
			            type: 'info',
			            message: ''
			          });          
			        });
            		
            		
            	}
            },
			xiaoBanDelete(data){
			        this.$confirm(', ?', '', {
				          confirmButtonText: '',
				          cancelButtonText: '',
				          type: 'warning'
				        }).then(() => {
				           
				           const id = data.id;
							this.$axios.delete(this.$config.serverUrl+"api/YZL_ZLXB_PY/DeleteYZL_ZLXB_PY/"+id, 
            							null,
					                	{headers: {'Content-Type': 'application/json'}}
					               ).then((res) => {
					                	this.getDataXB();
					                	this.$message.success("");
					                }).catch(error=>{
					                
		                				this.$message.error(error);
		                			})
				           
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: ''
				          });          
				        }); 
            },
		}
	}
</script>

<style>
	@import "../css/proCommon.css";
</style>


